.Ymacs {
  --ymacs-cursor-bg: orange;
  --ymacs-cursor-fg: #000;
  --ymacs-popup-bg: #fea;
  --ymacs-popup-fg: #000;
  --ymacs-popup-scrollbar-thumb: #000;
  --ymacs-popup-scrollbar-track: transparent;
  --ymacs-popup-border: #000;
}

.Ymacs, .Ymacs * {
  -webkit-user-select: none;
  user-select: none;
  box-sizing: border-box !important;
}

.Ymacs {
  flex-direction: column;
  width: 100%;
  height: 100%;
  font-family: Iosevka Custom, Iosevka Web, Iosevka, Ubuntu Mono, DejaVu Sans Mono, Lucida Sans Typewriter, monospace;
  display: flex;
  position: relative;
}

.Ymacs_Frame {
  cursor: default;
  flex-direction: column;
  flex-grow: 1;
  display: flex;
  overflow: hidden;
  container-type: inline-size;
}

.Ymacs_Frame.Ymacs_Frame-hasOverlays[data-ymacs-overlays~="selection"] .Ymacs-current-line, .Ymacs_Frame.Ymacs_Frame-hasOverlays[data-ymacs-overlays~="isearch"] .Ymacs-current-line {
  background-color: inherit;
}

.Ymacs-frame-overlays {
  flex-grow: 1;
  position: relative;
  overflow: hidden;
}

.Ymacs_Modeline {
  white-space: pre;
  flex-grow: 0;
  flex-shrink: 0;
  padding-bottom: 1px;
  overflow: hidden;
}

.Ymacs-frame-content {
  white-space: pre;
  z-index: 20;
  padding-bottom: 100cqh;
  position: relative;
}

.Ymacs_Minibuffer {
  flex-grow: 0;
  flex-shrink: 0;
}

.Ymacs_Minibuffer .Ymacs_Modeline {
  display: none;
}

.Ymacs_Overlay > div {
  position: absolute;
  left: 0;
  right: 0;
}

.Ymacs_Overlay.isearch > div {
  z-index: 1;
}

.Ymacs_Overlay.match-paren {
  opacity: .6;
}

.Ymacs_Overlay.match-paren > div {
  z-index: 30;
}

.Ymacs.Ymacs-line-numbers .Ymacs-frame-content {
  counter-reset: ymacs-line 0;
}

.Ymacs.Ymacs-line-numbers .Ymacs-frame-content:before {
  box-sizing: border-box;
  content: "";
  z-index: -1;
  width: 3.1em;
  font-size: 80%;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
}

.Ymacs.Ymacs-line-numbers .Ymacs-frame-content > div.line {
  counter-increment: ymacs-line;
}

.Ymacs.Ymacs-line-numbers .Ymacs-frame-content > div.line:before {
  box-sizing: border-box;
  content: counter(ymacs-line, decimal);
  text-align: right;
  width: 3.1em;
  margin-right: 7px;
  padding-right: 7px;
  font-size: 80%;
  display: inline-block;
  position: relative;
}

.Ymacs .Ymacs_Frame.Ymacs_Minibuffer .Ymacs-frame-content {
  padding: 0;
}

.Ymacs .Ymacs_Frame.Ymacs_Minibuffer .Ymacs-frame-content:before, .Ymacs .Ymacs_Frame.Ymacs_Minibuffer .Ymacs-frame-content div.line:before {
  content: none;
  display: none;
}

.Ymacs_Frame .minibuffer-prompt {
  font-weight: bold;
  text-decoration: underline;
}

.Ymacs .Ymacs_SplitCont {
  display: grid;
  overflow: hidden;
}

.Ymacs .Ymacs_SplitCont > .bar {
  z-index: 30;
  background-color: #888;
  position: relative;
}

.Ymacs .Ymacs_SplitCont > .bar:before {
  content: "";
  display: block;
  position: absolute;
  inset: 0;
}

.Ymacs .Ymacs_SplitCont.dragging > .bar {
  background-color: var(--ymacs-cursor-bg);
}

.Ymacs .Ymacs_SplitCont.horiz {
  cursor: ns-resize;
  grid-template-rows: 1fr auto 1fr;
}

.Ymacs .Ymacs_SplitCont.horiz > .bar {
  height: 3px;
}

.Ymacs .Ymacs_SplitCont.horiz > .bar:before {
  top: -5px;
  bottom: -5px;
}

.Ymacs .Ymacs_SplitCont.vert {
  cursor: ew-resize;
  grid-template-columns: 1fr auto 1fr;
}

.Ymacs .Ymacs_SplitCont.vert > .bar {
  width: 3px;
}

.Ymacs .Ymacs_SplitCont.vert > .bar:before {
  left: -5px;
  right: -5px;
}

.Ymacs_Resize_horiz {
  cursor: ns-resize;
}

.Ymacs_Resize_vert {
  cursor: ew-resize;
}

.Ymacs_Frame:focus {
  outline: none;
}

@keyframes ymacs-caret-block-blink {
  0% {
    background-color: unset;
  }

  50% {
    background-color: var(--ymacs-cursor-bg);
    color: var(--ymacs-cursor-fg);
  }
}

.Ymacs-cursor-block .Ymacs-overwrite-mode .Ymacs-caret {
  border-radius: 15px;
}

.Ymacs-cursor-block .Ymacs_Frame .Ymacs-caret {
  outline: 1px solid var(--ymacs-cursor-bg);
  outline-offset: -1px;
}

.Ymacs-cursor-block .Ymacs_Minibuffer .Ymacs-caret {
  background-color: inherit;
  color: inherit;
  outline: none;
}

.Ymacs-cursor-block .Ymacs_Frame:focus .Ymacs-caret {
  outline: none;
  animation-name: ymacs-caret-block-blink;
  animation-duration: .8s;
  animation-timing-function: step-start;
  animation-iteration-count: infinite;
  animation-fill-mode: backwards;
}

.Ymacs-cursor-bar {
  --ymacs-cursor-bar: var(--ymacs-cursor-bg);
}

@keyframes ymacs-caret-bar-blink {
  0% {
    --ymacs-cursor-bar: var(--ymacs-cursor-bg);
  }

  50% {
    --ymacs-cursor-bar: #0000;
  }
}

.Ymacs-cursor-bar .Ymacs_Minibuffer .Ymacs-caret {
  box-shadow: none;
}

.Ymacs-cursor-bar .Ymacs_Frame:focus .Ymacs-caret {
  box-shadow: -1px 0px 0px 0px var(--ymacs-cursor-bar), inset 1px 0px 0px 0px var(--ymacs-cursor-bar);
  animation-name: ymacs-caret-bar-blink;
  animation-duration: .8s;
  animation-timing-function: step-start;
  animation-iteration-count: infinite;
  animation-delay: .8s;
}

.Ymacs_Popup {
  background-color: var(--ymacs-popup-bg);
  color: var(--ymacs-popup-fg);
  border: 1px solid var(--ymacs-popup-border);
  z-index: 40;
  scrollbar-color: var(--ymacs-popup-scrollbar-thumb) var(--ymacs-popup-scrollbar-track);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 8px #0000003d;
}

.Ymacs_Popup.with-arrow:before, .Ymacs_Popup.with-arrow:after {
  content: "";
  pointer-events: none;
  border: solid #0000;
  width: 0;
  height: 0;
  position: absolute;
}

.Ymacs_Popup.with-arrow:after {
  border-width: 10px;
}

.Ymacs_Popup.with-arrow:before {
  border-width: 11px;
}

.Ymacs_Popup.with-arrow.ppos-top-right:before, .Ymacs_Popup.with-arrow.ppos-top-left:before {
  border-top-color: var(--ymacs-popup-border);
}

.Ymacs_Popup.with-arrow.ppos-bot-right:before, .Ymacs_Popup.with-arrow.ppos-bot-left:before {
  border-bottom-color: var(--ymacs-popup-border);
}

.Ymacs_Popup.with-arrow.ppos-top-right:after, .Ymacs_Popup.with-arrow.ppos-top-left:after {
  border-top-color: var(--ymacs-popup-bg);
}

.Ymacs_Popup.with-arrow.ppos-bot-right:after, .Ymacs_Popup.with-arrow.ppos-bot-left:after {
  border-bottom-color: var(--ymacs-popup-bg);
}

.Ymacs_Popup.with-arrow.ppos-top-right {
  margin-top: -12px;
  margin-left: -32px;
}

.Ymacs_Popup.with-arrow.ppos-top-right:before {
  top: 100%;
  left: 20px;
}

.Ymacs_Popup.with-arrow.ppos-top-right:after {
  top: 100%;
  left: 21px;
}

.Ymacs_Popup.with-arrow.ppos-top-left {
  margin-top: -12px;
  margin-left: 32px;
}

.Ymacs_Popup.with-arrow.ppos-top-left:before {
  top: 100%;
  right: 20px;
}

.Ymacs_Popup.with-arrow.ppos-top-left:after {
  top: 100%;
  right: 21px;
}

.Ymacs_Popup.with-arrow.ppos-bot-right {
  margin-top: 12px;
  margin-left: -32px;
}

.Ymacs_Popup.with-arrow.ppos-bot-right:before {
  bottom: 100%;
  left: 20px;
}

.Ymacs_Popup.with-arrow.ppos-bot-right:after {
  bottom: 100%;
  left: 21px;
}

.Ymacs_Popup.with-arrow.ppos-bot-left {
  margin-top: 12px;
  margin-left: 32px;
}

.Ymacs_Popup.with-arrow.ppos-bot-left:before {
  bottom: 100%;
  right: 20px;
}

.Ymacs_Popup.with-arrow.ppos-bot-left:after {
  bottom: 100%;
  right: 21px;
}

.Ymacs_Popup .Ymacs_Menu {
  max-height: 20em;
  padding: 1em 1.5em;
  scroll-padding: 1em 1.5em;
  overflow: auto;
}

.Ymacs_Popup .Ymacs_Menu .Ymacs_Menu_Item {
  margin: 0 -1.5em;
  padding: 0 1.5em;
}

.Ymacs-frame-content .italic {
  font-style: italic;
}

.Ymacs-frame-content .bold {
  font-weight: bold;
}

.Ymacs-frame-content .link {
  text-decoration: underline;
}
